.basket {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100vh;
    backdrop-filter: blur(10px);
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.basket__container {
    background-color: var(--color-light);
    width: 90%;
    height: 90%;
    padding: 30px;
    border-radius: 5px;
    position: relative;
}

.basket__bar {
    display: flex;
    justify-content: space-between;
}

.basket__close {
    background-image: url(../Basket/close.png);
    background-size: cover;
    display: block;
    width: 30px;
    height: 30px;
}