.card {
    width: 240px;
    background-color: var(--color-light);
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
    margin: 15px;
    padding: 20px;
    transition-property: box-shadow;
    transition-duration: 0.3s;
}

.card:hover .card__img{
    transform: scale(1.1);
}

.card:hover {
    box-shadow: 0 0 15px var(--color-primary);
}

.card__img {
    height: 200px;
    margin-top: auto;
    transition-property: transform;
    transition-duration: 0.3s;
}

.card__name {
    font-weight: 600;
    color: var(--color-dark);
    align-self: flex-start;
    margin-bottom: 20px;
    font-size: 17px;
    text-transform: uppercase;
}

.card__price {
    color: var(--color-extra-dark);
    font-weight: 400;
    margin-top: 20px;
    align-self: flex-start;
}

.card__btn_select {
    width: 100%;
    padding: 10px;
    background-color: var(--color-primary);
    color: var(--color-light);
    outline: none;
    border: none;
    margin-top: 20px;
    font-size: 15px;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid var(--color-primary);
}


.card__btn_unselect {
    width: 100%;
    padding: 10px;
    background-color: var(--color-light);
    color: var(--color-primary);
    outline: none;
    border: none;
    margin-top: 20px;
    font-size: 15px;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition-duration: 0.3s;
    transition-property: color, background-color;
}

.card__btn_unselect:hover {
    background-color: var(--color-primary);
    color: var(--color-light);
}