.basketButton {
    color: var(--color-primary);
    text-decoration: none;
    background-color: var(--color-light);
    padding: 8px 15px;
    font-size: 16px;
    font-weight: 500;
    border-radius: 20px;
    transition-duration: 0.3s;
    transition-property: box-shadow;
}

.basketButton:hover {
    box-shadow: 0 0 15px var(--color-dark);
}